import React, { useEffect } from 'react';
import './Ending.scss'
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import * as d3 from 'd3';

gsap.registerPlugin(ScrollTrigger);

const Ending = () => {  
  const ending = React.createRef();
  const credit = React.createRef();
  const ending_chart = React.createRef();
  const circleChartC = React.createRef();
  const circleChartD = React.createRef();
  const height_c = 625;
  const height_d = 165;
  const width = 1265;
  const radius = 35;
  useEffect(() => {
    const svg_c = d3.select(circleChartC.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height_c)
    .append("g")

    const svg_d = d3.select(circleChartD.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height_d)
    .append("g")

    const circles_c = d3.range(92).map(i => ({
      x: 91.5 * (i % 14) + 20 + radius/2,
      y: 91.5 * Math.floor(i/14) + radius,
      color: i > 72 ? '#C41F1F' : '#fff'
    }));

    const circles_d = d3.range(20).map(i => ({
      x: 91.5 * (i % 14) + 20 + radius/2,
      y: 91.5 * Math.floor(i/14) + radius,
      color: i > 3 ? '#A1A1A1' : '#6DB76D'
    }));
    
    svg_c.selectAll("circle")
      .data(circles_c)
      .join("circle")
      .attr("cx", function(d){ return d.x})
      .attr("cy", function(d){ return d.y})
      .attr("r", radius)
      .attr("fill", d => d.color)
    
    svg_d.selectAll("circle")
      .data(circles_d)
      .join("circle")
      .attr("cx", function(d){ return d.x})
      .attr("cy", function(d){ return d.y})
      .attr("r", radius)
      .attr("fill", d => d.color)
    
    gsap.timeline({scrollTrigger:{
      trigger: ending.current,
      scrub: true,
      pin: ending_chart.current,
      start: "top top",
      end: "+=120%"
    }})
    .from(credit.current, {opacity: 0, ease: "none"})

  });
  return (
    <div className="ending__wrapper" ref={ending}>
      <div className="ending cover__chart" ref={ending_chart}>
        <div className="cover__chart__b" ref={circleChartC}>
          <div className="cover__chartmark bottom">
            <div className="cover__charttitle">遭檢控人數</div>
            <div className="cover__chartnumber">1972</div>
          </div>
          <div className="cover__chartmark middle">
            <div className="cover__charttitle">反修例運動中的被捕人數</div>
            <div className="cover__chartnumber">9216</div>
          </div>
        </div>
        <div className="cover__chart__b" ref={circleChartD}>
          <div className="cover__chartmark bottom">
            <div className="cover__charttitle">待審理案件</div>
            <div className="cover__chartnumber">1668</div>
          </div>
          <div className="cover__chartmark middle">
            <div className="cover__charttitle">已結案</div>
            <div className="cover__chartnumber">304</div>
          </div>
        </div>
        <div className="ending__credit" ref={credit}>
          <h3>製作團隊</h3>
          <ul>
            <li><span>記者：</span><span>鄭佩珊</span></li>
            <li><span>攝影：</span><span>林振東、陳焯煇、劉子康、Stanley Leung</span></li>
            <li><span>資料搜集：</span><span>陳欣其、區婥媛、劉鈺怡、林可欣、鄺泳珊 </span></li>
            <li><span>影像設計：</span><span>林振東</span></li>
            <li><span>網頁製作：</span><span>Funkie Loopie</span></li>
            <li><span>協力：</span><span>曾立宇</span></li>
            <li><span>監製：</span><span>陳倩兒</span></li>
          </ul>
        </div>
      </div>
      
    </div>
  )
}

export default Ending;